import { autobind } from 'core-decorators'

import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import { Price } from '~/components'
import { CartItemQuantityContainer } from '~/containers'

import {
	Card,
	Button,
	Rate,
	message,
} from 'antd'

import './CatalogItem.css'

const { Meta } = Card

const DEFAULT_COUNT_ITEMS = 1

message.config({
	maxCount: 1,
	duration: 3,
})

export default @autobind class CatalogItem extends PureComponent {

	static propTypes = {
		id: PropTypes.number.isRequired,
		name: PropTypes.string.isRequired,
		desc: PropTypes.string.isRequired,
		price: PropTypes.number.isRequired,
		image: PropTypes.string.isRequired,
		rate: PropTypes.number.isRequired,
		stock: PropTypes.number.isRequired,
		addItemToCart: PropTypes.func.isRequired,
		removeItemFromCart: PropTypes.func.isRequired,
		changeCartItemCount: PropTypes.func.isRequired,
		countInCart: PropTypes.number.isRequired,
	}

	constructor(...args) {
		super(...args)

		this.count = DEFAULT_COUNT_ITEMS
	}

	componentDidUpdate(prevProps) {
		const { countInCart: prevCountInCart } = prevProps
		const { countInCart, name } = this.props

		if (countInCart !== prevCountInCart) {
			let text

			if (prevCountInCart === 0) {
				text = `"${ name }" was added to cart`
			} else if (countInCart === 0) {
				text = `"${ name }" was removed from cart`
			} else {
				text = `Changed the quantity of "${ name }" to ${ countInCart }`
			}

			message.success(text)
		}
	}

	onCountChange(count) {
		this.count = count
	}

	starsRate() {
		const { rate } = this.props

		return Math.round(rate * 2) / 2
	}

	cartButton() {
		const {
			id,
			image,
			name,
			desc,
			price,
			stock,
			countInCart,
			addItemToCart,
			removeItemFromCart,
		} = this.props

		const params = (countInCart > 0) ? {
			action: removeItemFromCart,
			text: 'Remove from cart',
			type: 'danger',
		} : {
			action: addItemToCart,
			text: 'Add to cart',
			type: 'default',
		}

		return (
			<Button
				type={ params.type }
				block
				onClick={ () => {
					params.action({
						id,
						image,
						name,
						desc,
						price,
						stock,
						count: this.count,
					})
				} }
				icon="shopping-cart"
			>
				{ params.text }
			</Button>
		)
	}

	bottom() {
		const { id, stock, countInCart } = this.props

		return (
			<div className="CatalogItem__bottom">
				{ this.cartButton() }
				<CartItemQuantityContainer
					{ ...{
						className: 'CatalogItem__count',
						itemId: id,
						countInCart,
						maxValue: stock,
						defaultValue: DEFAULT_COUNT_ITEMS,
						onChange: this.onCountChange,
					} }
				/>
			</div>
		)
	}

	render() {
		const {
			name,
			desc,
			price,
			image,
			rate,
		} = this.props

		return (
			<Card
				className="CatalogItem"
				hoverable
				cover={ (
					<img
						className="CatalogItem__image"
						alt={ name }
						src={ image }
					/>
				) }
			>
				<Meta
					title={ name }
					description={ desc }
				/>
				<Price
					className="CatalogItem__price"
					price={ price }
				/>
				<Rate
					className="CatalogItem__rate"
					allowHalf
					disabled
					defaultValue={ this.starsRate({ rate }) }
				/>
				{ this.bottom() }
			</Card>
		)
	}
}
